.page-wrapper {
  padding-top: 70px;
  padding-bottom: 0;
}

.tagline-primary {
  font-size: 5em;
  font-weight: 510;
  color: var(--primary);
  text-align: center;
  margin: 0;
  text-underline-offset: 30px;
}

.tagline-secondary {
  font-size: 1.5em;
  font-weight: 510;
  color: black;
  text-align: center;
  margin: 0;
  margin-top: 30px;
}

.underline {
  text-decoration: underline;
}

.tagline-wrapper {
  display: flex;
  flex-direction: column;
  padding: 25px;
}

.fixed-scroll-image {
  background-blend-mode: overlay;
  background-position: center;
  background-repeat: no-repeat;
  /* background-attachment: fixed; */
  background-size: 100%, auto;
  overflow: hidden;
  position: relative;
}

.article-cards-wrapper {
  padding: 0 50px 0 50px;
}

.image-button-wrapper {
  padding: 0 5px 5px 5px;
}

.quote-wrapper {
  height: 200px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
}

.quote {
  width: 50%;
  text-align: center;
  margin: 0;
  font-size: 2em;
  color: var(--fontPrimary);
}

.quotee {
  font-size: 1.5em;
  text-align: center;
  color: white;
  font-weight: bold;
  margin: 0;
}

@media screen and (max-width: 960px) {
  .tagline-primary {
    font-size: 4em;
  }
  .tagline-secondary {
    text-align: center;
    margin-top: 30px;
    margin-left: 0;
    margin-right: 0;
  }
  .image-button-wrapper {
    padding: 0 20px 20px 20px;
  }
  .fixed-scroll-image {
    background-attachment: scroll;
  }
}

@media screen and (max-width: 780px) {
  .tagline-primary {
    font-size: 3.5em;
  }
  .tagline-secondary {
    font-size: 1.4em;
  }
}

@media screen and (max-width: 700px) {
  .email-form-first-row {
    flex-direction: column;
  }
  .email-forename,
  .email-surname {
    width: 100%;
  }
  .email-input-label {
    font-size: 1.8em;
    width: 45%;
  }
  .quote {
    width: 85%;
    font-size: 1.6em;
  }
  .quotee {
    font-size: 1.3em;
  }
}
@media screen and (max-width: 510px) {
  .email-input-label {
    font-size: 1.4em;
    width: 45%;
  }
}

@media screen and (max-width: 410px) {
  .tagline-primary {
    font-size: 3em;
  }
  .tagline-secondary {
    font-size: 1.4em;
  }

  .quote-wrapper {
    height: auto;
  }
  .quote {
    width: 85%;
    font-size: 1.4em;
  }
  .quotee {
    font-size: 1.1em;
  }
}
